.page-header__toggle {
  display: block;
  width: 32px;
  height: 32px;
  padding: 4px 4px;
  background-color: inherit;
  cursor: pointer;
  border: none;
  justify-self: end;
  align-self: end;
  position: relative;
}
@media (min-width: 768px) {
  .page-header__toggle {
    display: none;
  }
}

.page-header__toggle::before {
  content: "";
  position: absolute;
  top: 6px;
  left: 6px;
  width: 24px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}

.page-header__toggle--close {
  top: 0;
  left: 0;
}
.page-header__toggle--close::before, .page-header__toggle--close::after {
  content: "";
  position: absolute;
  top: 15px;
  left: 0;
  width: 30px;
  height: 2px;
  background-color: #000000;
}
.page-header__toggle--close::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.page-header__toggle--close::after {
  transform: rotate(-45deg);
}

@media (max-width: 767px) {
  .page-header__nav {
    position: absolute;
    left: 0;
    width: 100%;
    background-color: #ffffff;
    z-index: 2;
  }
}
.page-header--no-js .page-header__nav {
  position: static;
}

.page-header--no-js .page-header__toggle {
  display: none;
}

.page-header--closed .page-header__toggle--close {
  display: none;
}

.page-header--closed .page-header__toggle {
  display: block;
}

.page-header--closed .page-header__nav {
  display: none;
}

