  width: 30px;
  height: 30px;
  margin: 0;
  opacity: 0;
  z-index: 2;
}
@media (min-width: 768px) {
  .control__input {
    width: 40px;
    height: 40px;
  }
}

.control__label::before {
  content: "";
  width: 30px;
  height: 30px;
  border: 1px solid #cdcdcd;
  position: absolute;
  left: 0;
  top: 0;
}
@media (min-width: 768px) {
  .control__label::before {
    width: 40px;
    height: 40px;
  }
}

.form-radio__area .control__label::before {
  border-radius: 50%;
}

.control__input[type=radio]:checked + .control__label::after {
  content: "";
  width: 14px;
  height: 14px;
  border-radius: 50%;
  background-color: #68b738;
  position: absolute;
  left: 8px;
  top: 8px;
}
@media (min-width: 768px) {
  .control__input[type=radio]:checked + .control__label::after {
    width: 18px;
    height: 18px;
    left: 11px;
    top: 11px;
  }
}

.control__input[type=checkbox]:checked + .control__label::after {
  content: "";
  width: 18px;
  height: 14px;
  background-image: url("../img/icons/tick.svg");
  background-repeat: no-repeat;
  position: absolute;
  left: 6px;
  top: 8px;
}
@media (min-width: 768px) {
  .control__input[type=checkbox]:checked + .control__label::after {
    width: 18px;
    height: 18px;
    left: 11px;
    top: 11px;
  }
}

.control__input:hover + .control__label::before {
  border-color: #cdcdcd;
}

.control__input:focus + .control__label::before {
  border-color: #68b738;
}

.control__input:disabled + .control__label::before,
.control__input:disabled + .control__label {
  opacity: 0.3;
  border-color: #bababa;
}